import { Component, OnInit } from '@angular/core';
import { FormBuilder,  Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { RecaptchaErrorParameters } from 'ng-recaptcha';
import { UserNewService } from 'src/app/services/user-new.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  public resolved(captchaResponse: string): void {
    console.log(`Resolved captcha with response: ${captchaResponse}`);
    // Bienvenido humano
  }

  public onError(errorDetails: RecaptchaErrorParameters): void {
    console.log(`reCAPTCHA error encountered; details:`, errorDetails);
    // No eres humano
  }

  loginForm = this.fb.group ({
    email: ['', Validators.required],
    passwordUsuario: ['', Validators.required]
	});
  
  constructor(private fb: FormBuilder, 
    private readonly userService: UserNewService,
    private router: Router) {
 
  }

  login(dataLogin){
    this.userService.login(dataLogin).subscribe((rest: any) => {
      if(rest.isSuccess){
        sessionStorage.setItem('token', rest.data.token);
        sessionStorage.setItem('user', rest.data.nombres + ' ' + rest.data.apellidoPaterno);
        sessionStorage.setItem('perfil', rest.data.idPerfil);
        this.router.navigateByUrl('/home', { skipLocationChange: false }).then(() => {
          this.router.navigate(['home']);
          window.location.reload();
        })
      } else{
        alert('Credenciales inválidas');
      }
    });
  }
  
  onSubmit(){
    if(this.loginForm.valid){
      this.login(this.loginForm.value);
      
    } else {
        alert("Formulario no valido");
      }
    }
  ngOnInit(): void {
  }

}
